.gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: min(0.5rem, 1vw);
  padding: 1rem;
}

@media (max-width: 700px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
  }
}

.imgButton {
  height: min-content;
  overflow: hidden;
}

.imgButton img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

button {
  display: flex;
  align-self: center;
  padding: 5px;
}

button:hover {
  background-color: var(--text-primary);
}

.artPopup {
  background-color: var(--background-color);
  display: flex;
  position: fixed;
  width: 100vw;
  left: 0;
  top: 0;
  height: 100vh;
  z-index: 999;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
  overflow-y: scroll;
  flex-direction: column;
}

@media (max-width: 900px) {
  .artPiece {
    flex-wrap: wrap;
    justify-content: center;
  }

  .artPopup {
    justify-content: center;
  }
  /* 
  .artPopup button {
    display: none;
  } */
}

.artPopup button {
  color: var(--text-primary);
  font-size: 2rem;
  justify-self: flex-end;
  margin: 1rem;
  padding: 2rem;
  border-radius: 6px;
}

.artPopup button img {
  width: 2rem;
}

.artPopup button:hover {
  background-color: var(--primary-color);
}

.artPopup .goLeft {
  position: absolute;
  left: 2rem;
}

.artPopup .goRight {
  position: absolute;
  right: 2rem;
}

.artPiece {
  display: flex;
  gap: 2rem;
  width: 100vw;
  font-size: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}

.artPiece .displayImg {
  max-width: 80vw;
  max-height: 80vh;
  object-fit: contain;
  margin: 2rem;
}

.artPiece p {
  display: flex;
  flex-direction: column;
  max-width: 40vw;
  flex-wrap: wrap;
  overflow-wrap: break-word;
}

.artPopup .exitPieceButton {
  color: var(--primary-color);
  position: absolute;
  top: 0;
  right: 0rem;
}

.artPopup .exitPieceButton img {
  width: 3rem;
}

.bottomNavBar {
  display: flex;
  width: 100vw;
  background-color: var(--background-color);
  justify-content: space-between;
  display: none;
}
