.project {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 1rem;
  color: var(--text-secondary);
}

strong,
b {
  color: var(--text-primary);
}

.projectTitle a {
  display: flex;
  align-items: center;
  text-decoration: none;
  gap: 0.5rem;
  color: var(--text-secondary);
}

@media (max-width: 700px) {
  .projectTitle {
    display: flex;
    max-width: 50vw;
    flex-wrap: wrap;
  }
}

.projectTitle a:hover {
  text-decoration: underline;
}

/* project gallery images */
.projectGallery {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}

.projectImg {
  height: 20vh;
  width: auto;
  max-width: min(60vw);
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

@media (max-width: 700px) {
  .projectImg {
    width: max-content;
    height: auto;
  }
}

.projectImg:hover {
  transform: scale(1.7);
}
